.Movie {
  min-width: 211px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  background-color: #fff;
  width: 40%;
  margin: 2% 2% 50px;
  padding: 20px;
  -webkit-box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3),
    0 5px 12px rgba(133, 133, 133, 0.22);
          box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3),
    0 5px 12px rgba(133, 133, 133, 0.22);
}

.Movie__Column {
  padding: 0 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Movie__Column:first-child {
  width: 40%;
}

.Movie__Column:last-child {
  width: 60%;
}

.Movie .title {
  font-size: 20px;
  line-height: 23px;
  font-weight: 600;
  padding-bottom: 5px;
  font-family: Helvetica, Arial, sans-serif;
  letter-spacing: 0.5px;
}

.Movie .Movie__Genres {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 10px;
}

.Movie__Genres .Movie__Genre {
  font-size: 12px;
  line-height: 15px;
  font-weight: 300;
  color: #b4b5bd;
}

.Movie__Genre + .Movie__Genre {
  margin-left: 5px;
}

.Movie__Genre + .Movie__Genre::before {
  content: '/';
  display: inline-block;
  vertical-align: top;
  margin-right: 5px;
}

.Movie .Movie__Synopsis {
  padding-top: 15px;
  border-top: 1px solid #f1f1f1;
  color: #7d7d7d;
  font-weight: 300;
  line-height: 18px;
}

.Movie .Movie__Poster {
  max-width: 100%;
  position: relative;
  top: -30px;
  -webkit-box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3),
    10px 15px 12px rgba(80, 80, 80, 0.22);
          box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3),
    10px 15px 12px rgba(80, 80, 80, 0.22);
}

@media screen and (min-width: 320px) and (max-width: 667px) {
  .Movie {
    width: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
  .Movie {
    width: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .Movie__Poster {
    top: 0;
    left: 0;
    width: 100%;
  }
  .Movie__Column {
    width: 100% !important;
  }
}



