body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol';
  background-color: #eff3f7;
  height: 100%;
  min-width: 320px;
}

html,
#root {
  height: 100%;
}

h1,
h2,
h3,
p,
ul {
  padding: 0;
  margin: 0;
}

li {
  display: inline-block;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}



